:root {
  --pp-color: #e94556;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 30px;
  }
  h2 {
    font-size: 15px;
  }
}

@media screen and (max-width: 900px) {
  header {
    height: 300px;
  }
  h1 {
    font-size: 45px;
  }
  h2 {
    font-size: 20px;
  }
  .credentials {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (min-width: 900px) {
  header {
    height: 400px;
  }
  h1 {
    font-size: 60px;
  }
  h2 {
    font-size: 45px;
  }
  .credentials {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
  }
}

@media screen and (min-width: 1250px) {
  header {
    height: 500px;
  }

}

header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 0 0 5%;
  width: 100vw;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("/src/images/marcus-reubenstein-8mXAvhTwE7I-unsplash.png");
  background-size: cover;
  background-position: center;
}

h1 {
  margin-bottom: 25px;
  display: inline-block;
  color: #d4102a;
  font-weight: 400;
}

h2 {
  color: #e1e1e1;
  font-weight: 300;
}

.credentials {
  width: 100vw;
  padding-top: 50px;
}

.credentials > div {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5% 10%;
}

.credentials > div > img {
  width: 100px;
}

main {
  padding: 5% 10%;
}

.about > h3 {
  color: #d4102a;
  font-size: 30px;
}
